-state {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: center;\n  padding: 40px 20px;\n  color: $text-muted;\n\n  i {\n    font-size: 36px;\n    margin-bottom: 12px;\n    opacity: 0.5;\n  }\n\n  p {\n    margin: 0;\n    font-size: 13px;\n  }\n}\n\n.history-content {\n  display: flex;\n  flex-direction: column;\n}\n\n.record-section {\n  border-bottom: 1px solid $border-color;\n\n  &:last-child {\n    border-bottom: none;\n  }\n}\n\n.section-header {\n  padding: 10px 16px;\n  cursor: pointer;\n  user-select: none;\n  transition: background 0.2s ease;\n\n  &:hover {\n    background: $bg-hover;\n  }\n}\n\n.section-title {\n  display: flex;\n  align-items: center;\n  gap: 8px;\n  font-size: 13px;\n  font-weight: 500;\n  color: $text-color;\n\n  i {\n    font-size: 11px;\n    transition: transform 0.2s ease;\n  }\n}\n\n.section-count {\n  padding: 1px 6px;\n  border-radius: 999px;\n  background: rgba(100, 180, 255, 0.12);\n  color: rgba(100, 200, 255, 0.9);\n  font-size: 10px;\n  font-weight: 600;\n}\n\n.records-container {\n  display: flex;\n  flex-direction: column;\n}\n\n.records-list {\n  min-height: 100px;\n}\n\n.record-item {\n  padding: 12px 16px;\n  border-top: 1px dashed rgba(255, 255, 255, 0.05);\n  transition: background 0.2s ease;\n\n  &:first-child {\n    border-top: none;\n  }\n\n  &:hover {\n    background: $bg-hover;\n  }\n}\n\n.manager-record {\n  border-left: 3px solid rgba(100, 180, 255, 0.5);\n}\n\n.external-record {\n  border-left: 3px solid rgba(255, 193, 7, 0.5);\n}\n\n.record-header {\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n  margin-bottom: 8px;\n}\n\n.record-path {\n  font-size: 11px;\n  font-family: var(--monoFontFamily, 'Consolas', monospace);\n  color: $em-color;\n  font-weight: 600;\n}\n\n.record-time {\n  font-size: 10px;\n  color: $text-muted;\n}\n\n.record-changes {\n  display: flex;\n  align-items: center;\n  gap: 8px;\n}\n\n.value-change {\n  flex: 1;\n  display: flex;\n  flex-direction: column;\n  gap: 4px;\n  padding: 6px 8px;\n  border-radius: 4px;\n  font-size: 11px;\n\n  .label {\n    color: $text-muted;\n    font-size: 9px;\n    font-weight: 600;\n    text-transform: uppercase;\n  }\n\n  .value {\n    font-family: var(--monoFontFamily, 'Consolas', monospace);\n    color: $text-color;\n    word-break: break-all;\n  }\n}\n\n.old-value {\n  background: rgba(255, 100, 100, 0.08);\n  border: 1px solid rgba(255, 100, 100, 0.2);\n}\n\n.new-value {\n  background: rgba(100, 220, 120, 0.08);\n  border: 1px solid rgba(100, 220, 120, 0.2);\n}\n\n.arrow-icon {\n  font-size: 10px;\n  color: $text-muted;\n  flex-shrink: 0;\n}\n\n.no-records {\n  padding: 20px;\n  text-align: center;\n  color: $text-muted;\n  font-size: 12px;\n}\n.floor-groups {\n  display: flex;\n  flex-direction: column;\n}\n\n.floor-group {\n  border-bottom: 1px solid rgba(255, 255, 255, 0.05);\n\n  &:last-child {\n    border-bottom: none;\n  }\n}\n\n.floor-header {\n  padding: 10px 16px;\n  cursor: pointer;\n  user-select: none;\n  transition: background 0.2s ease;\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n  background: rgba(255, 193, 7, 0.05);\n  border-left: 3px solid rgba(255, 193, 7, 0.5);\n\n  &:hover {\n    background: rgba(255, 193, 7, 0.12);\n  }\n}\n\n.floor-info {\n  display: flex;\n  align-items: center;\n  gap: 8px;\n  font-size: 12px;\n  color: $text-color;\n\n  i {\n    font-size: 10px;\n    transition: transform 0.2s ease;\n  }\n}\n\n.floor-label {\n  font-weight: 600;\n  color: rgba(255, 193, 7, 0.95);\n}\n\n.changes-count {\n  padding: 2px 6px;\n  border-radius: 999px;\n  background: rgba(255, 193, 7, 0.15);\n  color: rgba(255, 193, 7, 0.95);\n  font-size: 10px;\n  font-weight: 600;\n}\n\n.floor-time {\n  font-size: 10px;\n  color: $text-muted;\n}\n\n.floor-records {\n  background: rgba(0, 0, 0, 0.1);\n}\n\n\n.expand-enter-active,\n.expand-leave-active {\n  transition: all 0.3s ease;\n  overflow: hidden;\n}\n\n.expand-enter-from,\n.expand-leave-to {\n  opacity: 0;\n  max-height: 0;\n}\n\n.expand-enter-to,\n.expand-leave-from {\n  opacity: 1;\n  max-height: 500px;\n}\n\n.pagination {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  gap: 12px;\n  padding: 12px 16px;\n  border-top: 1px dashed rgba(255, 255, 255, 0.05);\n}\n\n.page-btn {\n  width: 32px;\n  height: 32px;\n  border-radius: 4px;\n  border: 1.5px solid $border-color;\n  background: transparent;\n  color: $text-color;\n  cursor: pointer;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  transition: all 0.2s ease;\n\n  &:hover:not(:disabled) {\n    background: $bg-hover;\n    border-color: $em-color;\n  }\n\n  &:disabled {\n    opacity: 0.3;\n    cursor: not-allowed;\n  }\n\n  i {\n    font-size: 12px;\n  }\n}\n\n.page-info {\n  font-size: 12px;\n  color: $text-muted;\n  min-width: 60px;\n  text-align: center;\n}\n"],"sourceRoot":""}]);
