 }\n\n  .fa-star {\n    color: $star-color;\n    font-size: 13px;\n  }\n}\n\n.count-badge {\n  padding: 1px 6px;\n  border-radius: 999px;\n  background: rgba(255, 193, 7, 0.2);\n  color: $star-color;\n  font-size: 10px;\n  font-weight: 600;\n}\n\n.clear-btn {\n  width: 24px;\n  height: 24px;\n  border-radius: 4px;\n  border: none;\n  background: transparent;\n  color: $text-muted;\n  cursor: pointer;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  transition: all 0.2s ease;\n\n  &:hover {\n    background: rgba(255, 100, 100, 0.15);\n    color: rgba(255, 150, 150, 0.9);\n  }\n\n  i {\n    font-size: 11px;\n  }\n}\n\n.expand-enter-active,\n.expand-leave-active {\n  transition: all 0.3s ease;\n  overflow: hidden;\n}\n\n.expand-enter-from,\n.expand-leave-to {\n  opacity: 0;\n  max-height: 0;\n}\n\n.expand-enter-to,\n.expand-leave-from {\n  opacity: 1;\n  max-height: 500px;\n}\n\n.favorites-list {\n  display: flex;\n  flex-direction: column;\n  gap: 6px;\n  padding: 12px;\n}\n\n.favorite-item {\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n  gap: 12px;\n  padding: 10px 12px;\n  border-radius: $border-radius;\n  border: 1.5px solid $border-color;\n  background: transparent;\n  transition: all 0.2s ease;\n  position: relative;\n  overflow: hidden;\n\n  &:hover {\n    border-color: rgba(255, 193, 7, 0.5);\n    background: $bg-hover;\n\n    .favorite-actions {\n      opacity: 1;\n    }\n  }\n\n  &--editing {\n    border-color: $em-color;\n    background: $bg-hover;\n    padding: 8px 12px;\n  }\n\n  &--changed {\n    position: relative;\n    overflow: hidden;\n\n    &::after {\n      content: '';\n      position: absolute;\n      top: 0;\n      left: 0;\n      width: 150%;\n      height: 100%;\n      background: linear-gradient(\n        90deg,\n        transparent 0%,\n        rgba(80, 220, 120, 0.08) 15%,\n        rgba(70, 210, 110, 0.18) 25%,\n        rgba(60, 200, 100, 0.28) 35%,\n        rgba(50, 190, 90, 0.35) 45%,\n        rgba(60, 200, 100, 0.28) 55%,\n        rgba(70, 210, 110, 0.18) 65%,\n        rgba(80, 220, 120, 0.08) 75%,\n        transparent 85%\n      );\n      filter: blur(12px);\n      animation: favorite_update_completion 1.4s cubic-bezier(0.3, 0.0, 0.3, 1) forwards;\n      pointer-events: none;\n      z-index: 5;\n    }\n  }\n}\n\n@keyframes favorite_update_completion {\n  0% {\n    transform: translateX(-100%);\n    opacity: 0;\n  }\n  10% {\n    opacity: 0.4;\n  }\n  30% {\n    opacity: 0.8;\n  }\n  70% {\n    opacity: 0.8;\n  }\n  95% {\n    opacity: 0.4;\n  }\n  100% {\n    transform: translateX(100%);\n    opacity: 0;\n  }\n}\n\n.favorite-info {\n  display: flex;\n  flex-direction: column;\n  gap: 4px;\n  flex: 1;\n  min-width: 0;\n}\n\n.favorite-path {\n  font-size: 12px;\n  font-weight: 600;\n  color: $text-color;\n  font-family: var(--monoFontFamily, 'Consolas', monospace);\n  white-space: nowrap;\n  overflow: hidden;\n  text-overflow: ellipsis;\n}\n\n.favorite-value {\n  font-size: 12px;\n  color: $text-muted;\n  font-family: var(--monoFontFamily, 'Consolas', monospace);\n  white-space: nowrap;\n  overflow: hidden;\n  text-overflow: ellipsis;\n  padding: 4px 8px;\n  background: var(--SmartThemeBlurTintColor);\n  border-radius: 3px;\n  border: 1px solid $border-color;\n  max-width: 100%;\n  word-break: break-all;\n}\n\n.favorite-actions {\n  display: flex;\n  gap: 6px;\n  opacity: 0.5;\n  transition: opacity 0.2s ease;\n}\n\n.action-icon-btn {\n  width: 28px;\n  height: 28px;\n  border-radius: 4px;\n  border: 1px solid $border-color;\n  background: transparent;\n  color: $text-color;\n  cursor: pointer;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  transition: all 0.2s ease;\n\n  &:hover {\n    background: $bg-hover;\n  }\n\n  i {\n    font-size: 11px;\n  }\n}\n\n.edit-btn {\n  &:hover {\n    border-color: rgba(100, 180, 255, 0.6);\n    color: rgba(100, 200, 255, 0.9);\n  }\n}\n\n.remove-btn {\n  i {\n    color: $star-color;\n  }\n\n  &:hover {\n    border-color: rgba(255, 193, 7, 0.6);\n    background: rgba(255, 193, 7, 0.1);\n  }\n}\n\n.favorite-edit {\n  display: flex;\n  align-items: center;\n  gap: 12px;\n  width: 100%;\n}\n\n.edit-path {\n  font-size: 11px;\n  font-family: var(--monoFontFamily, 'Consolas', monospace);\n  color: $em-color;\n  font-weight: 600;\n  flex-shrink: 0;\n  max-width: 150px;\n  white-space: nowrap;\n  overflow: hidden;\n  text-overflow: ellipsis;\n}\n\n.favorite-edit :deep(.value-controls) {\n  flex: 1;\n  min-width: 0;\n}\n"],"sourceRoot":""}]);
