rder-color;\n  background: transparent;\n  color: $text-color;\n  cursor: default;\n  transition: all 0.2s ease;\n  overflow: visible;\n\n  &:hover {\n    border-color: $em-color;\n    background: $bg-hover;\n  }\n\n  &--adding {\n    border-bottom-left-radius: 0;\n    border-bottom-right-radius: 0;\n    margin-bottom: 0;\n  }\n\n  &--changed {\n    position: relative;\n    overflow: hidden;\n\n    &::after {\n      content: '';\n      position: absolute;\n      top: 0;\n      left: 0;\n      width: 150%;\n      height: 100%;\n      background: linear-gradient(\n        90deg,\n        transparent 0%,\n        rgba(80, 220, 120, 0.08) 15%,\n        rgba(70, 210, 110, 0.18) 25%,\n        rgba(60, 200, 100, 0.28) 35%,\n        rgba(50, 190, 90, 0.35) 45%,\n        rgba(60, 200, 100, 0.28) 55%,\n        rgba(70, 210, 110, 0.18) 65%,\n        rgba(80, 220, 120, 0.08) 75%,\n        transparent 85%\n      );\n      filter: blur(12px);\n      animation: variable_update_completion 1.4s cubic-bezier(0.3, 0.0, 0.3, 1) forwards;\n      pointer-events: none;\n      z-index: 5;\n    }\n  }\n\n  &--editing {\n    border-color: $em-color;\n    background: $bg-hover;\n    padding: 4px 8px;\n\n    .tree-node__label {\n      flex-shrink: 0;\n      min-width: auto;\n      max-width: 20px;\n    }\n\n    :deep(.value-controls) {\n      flex: 1;\n      min-width: 0;\n      width: 100%;\n    }\n  }\n\n  // 移动端优化\n  @media (max-width: $mobile-breakpoint) {\n    margin-left: calc(var(--node-depth) * 12px);\n    padding: 10px 12px;\n    gap: 10px;\n    min-height: 44px;\n    position: relative;\n    opacity: calc(1 - var(--node-depth) * 0.12);\n    border-width: calc(1.5px - var(--node-depth) * 0.2px);\n\n    &--editing {\n      padding: 8px 12px;\n      display: flex;\n      align-items: center;\n      gap: 8px;\n\n      .tree-node__label-group {\n        flex-shrink: 0;\n        min-width: fit-content;\n        max-width: 100px;\n      }\n\n      :deep(.value-controls) {\n        flex: 1;\n        min-width: 0;\n      }\n    }\n  }\n}\n\n.tree-node__depth-lines {\n  display: none;\n}\n\n.depth-line {\n  display: none;\n}\n\n.tree-node__toggle {\n  width: 24px;\n  height: 24px;\n  border-radius: 4px;\n  border: none;\n  background: transparent;\n  color: $text-color;\n  cursor: pointer;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  transition: all 0.2s ease;\n  flex-shrink: 0;\n\n  &:hover {\n    background: $bg-hover;\n    color: $em-color;\n  }\n\n  i {\n    font-size: 12px;\n  }\n\n  @media (max-width: $mobile-breakpoint) {\n    width: 36px;\n    height: 36px;\n\n    i {\n      font-size: 14px;\n    }\n  }\n}\n\n\n.tree-node__label-group {\n  display: inline-flex;\n  align-items: center;\n  gap: 0;\n  flex-shrink: 0;\n  flex-grow: 0;\n  background: $bg-hover;\n  border: 1px solid $border-color;\n  border-radius: 4px;\n  overflow: hidden;\n  user-select: none;\n  min-width: fit-content;\n  max-width: fit-content;\n\n  .label-text {\n    font-weight: 600;\n    font-size: 11px;\n    padding: 3px 6px;\n    color: $text-color;\n    background: $bg-hover;\n    font-family: var(--mainFontFamily, system-ui, -apple-system, sans-serif);\n    white-space: nowrap;\n  }\n\n  .type-text {\n    font-size: 9px;\n    padding: 3px 5px;\n    color: $text-muted;\n    font-weight: 500;\n    white-space: nowrap;\n    border-left: 1px solid $border-color;\n  }\n\n  @media (max-width: $mobile-breakpoint) {\n    .label-text {\n      font-size: 12px;\n      padding: 4px 7px;\n    }\n\n    .type-text {\n      font-size: 10px;\n      padding: 4px 6px;\n    }\n  }\n}\n\n.tree-node__label {\n  font-weight: 500;\n  font-size: 13px;\n  user-select: none;\n  flex: 1;\n  min-width: 0;\n  font-family: var(--mainFontFamily, system-ui, -apple-system, sans-serif);\n\n  @media (max-width: $mobile-breakpoint) {\n    font-size: 15px;\n  }\n}\n\n.tree-node__value-group {\n  display: flex;\n  align-items: center;\n  gap: 6px;\n  margin-left: auto;\n  flex-shrink: 1;\n}\n\n.tree-node__value {\n  margin-left: auto;\n  min-width: 80px;\n  max-width: 200px;\n  border: 1.5px solid $border-color;\n  border-radius: $border-radius;\n  background: transparent;\n  padding: 5px 10px;\n  cursor: pointer;\n  font-family: var(--monoFontFamily, 'Consolas', monospace);\n  font-size: 12px;\n  overflow: hidden;\n  text-overflow: ellipsis;\n  white-space: nowrap;\n  transition:\n    border-color 0.2s ease,\n    background 0.2s ease,\n    box-shadow 0.25s ease,\n    transform 0.3s cubic-bezier(0.4, 0, 0.2, 1);\n  flex-shrink: 1;\n  position: relative;\n\n  &:hover {\n    border-color: $em-color;\n    background: $bg-hover;\n  }\n\n  @media (max-width: $mobile-breakpoint) {\n    min-width: 80px;\n    max-width: 150px;\n    padding: 8px 12px;\n    font-size: 14px;\n    min-height: 36px;\n  }\n}\n\n.tree-node__favorite-btn {\n  width: 24px;\n  height: 24px;\n  border-radius: 4px;\n  border: 1px solid $border-color;\n  background: transparent;\n  color: $text-muted;\n  cursor: pointer;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  transition: all 0.2s ease;\n  flex-shrink: 0;\n  opacity: 0.5;\n\n  &:hover {\n    background: $bg-hover;\n    border-color: rgba(255, 193, 7, 0.5);\n    color: #ffc107;\n    opacity: 1;\n  }\n\n  &.is-favorite {\n    color: #ffc107;\n    border-color: rgba(255, 193, 7, 0.5);\n    background: rgba(255, 193, 7, 0.08);\n    opacity: 1;\n\n    &:hover {\n      background: rgba(255, 193, 7, 0.15);\n    }\n  }\n\n  i {\n    font-size: 11px;\n  }\n\n  @media (max-width: $mobile-breakpoint) {\n    width: 28px;\n    height: 28px;\n    opacity: 1;\n\n    i {\n      font-size: 12px;\n    }\n  }\n}\n\n.tree-node__row:hover .tree-node__favorite-btn {\n  opacity: 1;\n}\n\n.tree-node__row--changed .tree-node__value {\n  position: relative;\n  overflow: hidden;\n\n  &::after {\n    content: '';\n    position: absolute;\n    top: 0;\n    left: 0;\n    width: 150%;\n    height: 100%;\n    background: linear-gradient(\n      90deg,\n      transparent 0%,\n      rgba(80, 220, 120, 0.12) 15%,\n      rgba(70, 210, 110, 0.25) 25%,\n      rgba(60, 200, 100, 0.38) 35%,\n      rgba(50, 190, 90, 0.48) 45%,\n      rgba(60, 200, 100, 0.38) 55%,\n      rgba(70, 210, 110, 0.25) 65%,\n      rgba(80, 220, 120, 0.12) 75%,\n      transparent 85%\n    );\n    filter: blur(15px);\n    animation: variable_value_completion 1.2s cubic-bezier(0.3, 0.0, 0.3, 1) forwards;\n    pointer-events: none;\n    z-index: 5;\n  }\n}\n\n/* 变量更新完成动画 - Material Design 风格 */\n@keyframes variable_update_completion {\n  0% {\n    transform: translateX(-100%);\n    opacity: 0;\n  }\n  10% {\n    opacity: 0.4;\n  }\n  30% {\n    opacity: 0.8;\n  }\n  70% {\n    opacity: 0.8;\n  }\n  95% {\n    opacity: 0.4;\n  }\n  100% {\n    transform: translateX(100%);\n    opacity: 0;\n  }\n}\n\n/* 变量值更新完成动画 - 更强烈的效果 */\n@keyframes variable_value_completion {\n  0% {\n    transform: translateX(-100%);\n    opacity: 0;\n  }\n  8% {\n    opacity: 0.5;\n  }\n  25% {\n    opacity: 0.9;\n  }\n  75% {\n    opacity: 0.9;\n  }\n  92% {\n    opacity: 0.5;\n  }\n  100% {\n    transform: translateX(100%);\n    opacity: 0;\n  }\n}\n\n.tree-node__badge {\n  margin-left: auto;\n  padding: 3px 10px;\n  border-radius: 999px;\n  border: 1.5px solid $border-color;\n  background: transparent;\n  font-size: 11px;\n  color: $text-muted;\n  user-select: none;\n  flex-shrink: 0;\n  transition: all 0.2s ease;\n\n  @media (max-width: $mobile-breakpoint) {\n    padding: 4px 10px;\n    font-size: 11px;\n    font-weight: 600;\n  }\n}\n\n.tree-node__add-btn {\n  width: 20px;\n  height: 20px;\n  border-radius: 3px;\n  border: 1.5px solid $border-color;\n  background: transparent;\n  color: $text-color;\n  cursor: pointer;\n  display: inline-flex;\n  align-items: center;\n  justify-content: center;\n  transition: all 0.2s ease;\n  flex-shrink: 0;\n  margin-left: 8px;\n  opacity: 0.5;\n\n  &:hover {\n    background: $bg-hover;\n    border-color: $em-color;\n    color: $em-color;\n    opacity: 1;\n  }\n\n  i {\n    font-size: 10px;\n  }\n\n  @media (max-width: $mobile-breakpoint) {\n    width: 28px;\n    height: 28px;\n    opacity: 1;\n\n    i {\n      font-size: 12px;\n    }\n  }\n}\n\n.tree-node__row:hover .tree-node__add-btn {\n  opacity: 1;\n}\n\n.tree-node__children {\n  margin-top: 4px;\n\n  @media (max-width: $mobile-breakpoint) {\n    margin-top: 2px;\n    margin-bottom: 2px;\n  }\n}\n\n.expand-enter-active,\n.expand-leave-active {\n  transition: all 0.3s ease;\n  overflow: hidden;\n}\n\n.expand-enter-from,\n.expand-leave-to {\n  opacity: 0;\n  max-height: 0;\n}\n\n.expand-enter-to,\n.expand-leave-from {\n  opacity: 1;\n  max-height: 1000px;\n}\n\n.add-toolbar {\n  --node-depth: 0;\n  position: relative;\n  margin-left: calc(var(--node-depth) * 20px);\n  margin-bottom: 4px;\n  padding: 8px 12px;\n  border-radius: 0 0 $border-radius $border-radius;\n  border: 1.5px solid $border-color;\n  border-top: 1px dashed $border-color;\n  background: transparent;\n  display: flex;\n  gap: 8px;\n  align-items: center;\n  transition: all 0.2s ease;\n\n  @media (max-width: $mobile-breakpoint) {\n    margin-left: calc(var(--node-depth) * 12px);\n    padding: 8px 12px;\n    gap: 6px;\n    opacity: calc(1 - var(--node-depth) * 0.12);\n    min-height: 40px;\n  }\n}\n\n.expand-add-enter-active,\n.expand-add-leave-active {\n  transition: all 0.3s ease;\n  overflow: hidden;\n}\n\n.expand-add-enter-from,\n.expand-add-leave-to {\n  opacity: 0;\n  max-height: 0;\n  padding-top: 0;\n  padding-bottom: 0;\n}\n\n.expand-add-enter-to,\n.expand-add-leave-from {\n  opacity: 1;\n  max-height: 100px;\n}\n\n.add-input-row {\n  flex: 1;\n  display: flex;\n  gap: 4px;\n  align-items: center;\n  min-width: 0;\n\n  @media (max-width: $mobile-breakpoint) {\n    gap: 6px;\n    flex-wrap: wrap;\n  }\n}\n\n.action-btn {\n  height: 34px;\n  padding: 0 14px;\n  border-radius: $border-radius;\n  border: 1.5px solid $border-color;\n  background: transparent;